
import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './shuffle.scss';

const ShuffleButton = ({playerRef}) => { 
    let [lit, setLit] = useState(false);
    let isLit = useRef();
    let juiceValue = useRef();

    const onMessage = useCallback((e) => {
        let data = e.data
        if (Array.isArray(data)) {
            let juice = data[0];
            if (juice !== juiceValue.current) {
                if (new Date().getTime() - isLit.current < 1000) {
                    isLit.current = new Date().getTime();
                    return;
                }
                setLit(true);
                isLit.current = new Date().getTime();
                setTimeout(() => {
                    setLit(false);
                }, 1000);
            }
            juiceValue.current = juice;
        }
    }, [setLit]);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        return () => window.removeEventListener("message", onMessage, false);
    }, [setLit]);

    const onClickPrev = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "previous-shuffle-sample",
            body: true
        }, "*");
    }, [playerRef]);

    const onClickNext = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "next-shuffle-sample",
            body: true
        }, "*");
    }, [playerRef]);

    let className = "shuffle-buttons";
    if (lit) {
        className += " lit";
    }
    return (
        <div className={className}>
          <div className="pushable prev shuffle-btn" onClick={onClickPrev}>
            <div className="circle-icon"/>
          </div>
          <div className="pushable next shuffle-btn" onClick={onClickNext}>
            <div className="right-triangle-icon"/>
          </div>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ShuffleButton);
